.support {
  display: flex;
  flex-direction: column;
  z-index: 1;

  &__title {
    width: 400px;
    transform: rotate(-20deg);
    align-self: flex-start;
    margin-bottom: -120px;
  }

  &__description {
    align-self: flex-end;
    text-align: center;
    width: 60%;
    margin-right: 40px;
  }

  &__links {
    margin: 0px 0 30px;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 300px;
    // background-image: url('../../images/supportBackground.png');
    background-size: 90% 90%;
    background-position: center;
    background-repeat: no-repeat;
    align-self: center;
    display: flex;
    gap: 0;
  }

  &__link-icon {
    width: 100px;
    transition: width .5s ease;

    &:hover {
      cursor: pointer;
      width: 120px;
    }
  }

  .copy-alert {
    align-self: center;
    margin-top: -100px;
    margin-bottom: 50px;
    height: 20px;
  }
}

@media screen and (max-width: 450px) {
  .support {

    &__title {
      transform: none;
      margin-top: -30px;
    }

    &__description {
      align-self: center;
      margin: 0;
      width: 90%;
      margin-top: 40px;
    }

    &__links {
      margin-top: 30px;
      flex-wrap: wrap;
      column-gap: 20px;
      margin-bottom: 100px;
    }
  }
}